* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

/* Design Tokens (CSS Variables) */
:root {
    /* Spacing */
    --ds-spacing-xxs: 4px;
    --ds-spacing-xs: 8px;
    --ds-spacing-sm: 12px;
    --ds-spacing-md: 16px;
    --ds-spacing-lg: 24px;
    --ds-spacing-xl: 32px;
    --ds-spacing-xxl: 48px;
    --ds-spacing-page-padding: var(--ds-spacing-lg); /* Default page padding */

    /* Colors */
    --ds-color-primary: #007bff;
    --ds-color-secondary: #6c757d;
    --ds-color-text: #333;
    --ds-color-background: #f8f9fa;

    /* Typography */
    --ds-font-family-body: sans-serif;
    --ds-font-family-heading: serif;
    --ds-font-size-base: 16px;

    /* Page specific */
    --ds-page-max-width: 1200px;

    /* Form Colors */
    --ds-form-border-color: #ccc;
    --ds-form-focus-color: var(--ds-color-primary);
    --ds-form-error-color: #dc3545;
    --ds-form-bg-color: white;
    --ds-form-disabled-bg-color: #e9ecef;
    --ds-form-text-color: var(--ds-color-text);

    /* Form Spacing & Sizing */
    --ds-form-input-padding: 8px 12px;
    --ds-form-border-radius: 4px;
    --ds-form-line-height: 1.5;
}

/* Global base styles */
html {
    font-family: var(--ds-font-family-body);
    font-size: var(--ds-font-size-base);
    line-height: 1.5;
    color: var(--ds-color-text);
    background-color: var(--ds-color-background);
}

body {
    margin: 0;
    padding: 0;
}

/* Utility classes */
.ds-text-center {
    text-align: center;
}

.ds-text-left {
    text-align: left;
}

.ds-text-right {
    text-align: right;
}

.ds-margin-0 {
    margin: 0;
}

.ds-padding-0 {
    padding: 0;
}

/* Form Element Base Styles */
input[part="input"],
textarea[part="textarea"],
select[part="select"] {
    width: 100%;
    padding: var(--ds-form-input-padding);
    border: 1px solid var(--ds-form-border-color);
    border-radius: var(--ds-form-border-radius);
    font-family: var(--ds-font-family-body);
    font-size: var(--ds-font-size-base);
    line-height: var(--ds-form-line-height);
    background-color: var(--ds-form-bg-color);
    color: var(--ds-form-text-color);
    box-sizing: border-box;
    transition: border-color 0.2s ease-in-out;
}

input[part="input"]:focus,
textarea[part="textarea"]:focus,
select[part="select"]:focus {
    outline: none;
    border-color: var(--ds-form-focus-color);
    box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25);
}

input[part="input"]:disabled,
textarea[part="textarea"]:disabled,
select[part="select"]:disabled {
    background-color: var(--ds-form-disabled-bg-color);
    color: #6c757d;
    cursor: not-allowed;
}

input[part="input"]:read-only,
textarea[part="textarea"]:read-only {
    background-color: var(--ds-form-disabled-bg-color);
}

/* Button Styles */
button[part="button"] {
    padding: var(--ds-form-input-padding);
    border: 1px solid var(--ds-form-border-color);
    border-radius: var(--ds-form-border-radius);
    font-family: var(--ds-font-family-body);
    font-size: var(--ds-font-size-base);
    line-height: var(--ds-form-line-height);
    background-color: var(--ds-form-bg-color);
    color: var(--ds-form-text-color);
    cursor: pointer;
    transition: all 0.2s ease-in-out;
}

button[part="button"]:hover {
    background-color: #e9ecef;
}

button[part="button"]:focus {
    outline: none;
    border-color: var(--ds-form-focus-color);
    box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25);
}

button[part="button"]:disabled {
    background-color: var(--ds-form-disabled-bg-color);
    color: #6c757d;
    cursor: not-allowed;
}

/* Radio and Checkbox Styles */
input[part="radio"],
input[part="checkbox"] {
    margin-right: var(--ds-spacing-xs);
}

/* Label Styles */
label[part="label"] {
    display: block;
    margin-bottom: var(--ds-spacing-xs);
    font-weight: 500;
    color: var(--ds-form-text-color);
}

/* Fieldset and Legend Styles */
fieldset[part="fieldset"] {
    border: 1px solid var(--ds-form-border-color);
    border-radius: var(--ds-form-border-radius);
    padding: var(--ds-spacing-md);
    margin: var(--ds-spacing-md) 0;
}

legend[part="legend"] {
    padding: 0 var(--ds-spacing-xs);
    font-weight: 600;
    color: var(--ds-form-text-color);
}

/* Option Styles */
option[part="option"] {
    padding: var(--ds-spacing-xs);
} `, "",{"version":3,"sources":["webpack://./src/design_system/styles.css"],"names":[],"mappings":"AAAA,4BAA4B;AAC5B;IACI,sBAAsB;IACtB,SAAS;IACT,UAAU;AACd;;AAEA,kCAAkC;AAClC;IACI,YAAY;IACZ,qBAAqB;IACrB,oBAAoB;IACpB,qBAAqB;IACrB,qBAAqB;IACrB,qBAAqB;IACrB,qBAAqB;IACrB,sBAAsB;IACtB,+CAA+C,EAAE,yBAAyB;;IAE1E,WAAW;IACX,2BAA2B;IAC3B,6BAA6B;IAC7B,qBAAqB;IACrB,8BAA8B;;IAE9B,eAAe;IACf,iCAAiC;IACjC,+BAA+B;IAC/B,yBAAyB;;IAEzB,kBAAkB;IAClB,2BAA2B;;IAE3B,gBAAgB;IAChB,4BAA4B;IAC5B,8CAA8C;IAC9C,8BAA8B;IAC9B,yBAAyB;IACzB,oCAAoC;IACpC,0CAA0C;;IAE1C,0BAA0B;IAC1B,iCAAiC;IACjC,4BAA4B;IAC5B,0BAA0B;AAC9B;;AAEA,uBAAuB;AACvB;IACI,uCAAuC;IACvC,mCAAmC;IACnC,gBAAgB;IAChB,2BAA2B;IAC3B,4CAA4C;AAChD;;AAEA;IACI,SAAS;IACT,UAAU;AACd;;AAEA,oBAAoB;AACpB;IACI,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,SAAS;AACb;;AAEA;IACI,UAAU;AACd;;AAEA,6BAA6B;AAC7B;;;IAGI,WAAW;IACX,qCAAqC;IACrC,6CAA6C;IAC7C,2CAA2C;IAC3C,uCAAuC;IACvC,mCAAmC;IACnC,uCAAuC;IACvC,yCAAyC;IACzC,gCAAgC;IAChC,sBAAsB;IACtB,yCAAyC;AAC7C;;AAEA;;;IAGI,aAAa;IACb,wCAAwC;IACxC,6CAA6C;AACjD;;AAEA;;;IAGI,kDAAkD;IAClD,cAAc;IACd,mBAAmB;AACvB;;AAEA;;IAEI,kDAAkD;AACtD;;AAEA,kBAAkB;AAClB;IACI,qCAAqC;IACrC,6CAA6C;IAC7C,2CAA2C;IAC3C,uCAAuC;IACvC,mCAAmC;IACnC,uCAAuC;IACvC,yCAAyC;IACzC,gCAAgC;IAChC,eAAe;IACf,gCAAgC;AACpC;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,aAAa;IACb,wCAAwC;IACxC,6CAA6C;AACjD;;AAEA;IACI,kDAAkD;IAClD,cAAc;IACd,mBAAmB;AACvB;;AAEA,8BAA8B;AAC9B;;IAEI,kCAAkC;AACtC;;AAEA,iBAAiB;AACjB;IACI,cAAc;IACd,mCAAmC;IACnC,gBAAgB;IAChB,gCAAgC;AACpC;;AAEA,+BAA+B;AAC/B;IACI,6CAA6C;IAC7C,2CAA2C;IAC3C,6BAA6B;IAC7B,8BAA8B;AAClC;;AAEA;IACI,+BAA+B;IAC/B,gBAAgB;IAChB,gCAAgC;AACpC;;AAEA,kBAAkB;AAClB;IACI,6BAA6B;AACjC","sourcesContent":["/* CSS Reset/Normalization */\n* {\n    box-sizing: border-box;\n    margin: 0;\n    padding: 0;\n}\n\n/* Design Tokens (CSS Variables) */\n:root {\n    /* Spacing */\n    --ds-spacing-xxs: 4px;\n    --ds-spacing-xs: 8px;\n    --ds-spacing-sm: 12px;\n    --ds-spacing-md: 16px;\n    --ds-spacing-lg: 24px;\n    